import { branchConstants } from "./constants";
// actions
export const loadingAction = () => ({
  type: branchConstants.LOADING_BRANCH,
});
export const loadingUpdatedAction = () => ({
  type: branchConstants.LOADING_UPDATED,
});
export const getBranchSuccessAction = (data) => ({
  type: branchConstants.GET_BRANCH_SUCCESS,
  payload: data,
});
export const getBranchFailAction = (error) => ({
  type: branchConstants.GET_BRANCH_FAIL,
  payload: error,
});

export const createBranchSuccessAction = (data) => ({
  type: branchConstants.CREATE_BRANCH_SUCCESS,
  payload: data,
});

export const createBranchFailAction = (error) => ({
  type: branchConstants.CREATE_BRANCH_FAIL,
  payload: error,
});

export const deleteBranchSuccessAction = (data) => ({
  type: branchConstants.BRANCH_DELETED_SUCCESS,
  payload: data,
});

export const deleteBranchFailAction = (error) => ({
  type: branchConstants.BRANCH_DELETED_FAIL,
  payload: error,
});

export const updatedBranchSuccessAction = (data) => ({
  type: branchConstants.BRANCH_UPDATE_SUCCESS,
  payload: data,
});

export const updatedBranchFailAction = (error) => ({
  type: branchConstants.BRANCH_UPDATE_FAIL,
  payload: error,
});
