import React, { useState } from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import TextError from "../../Component/TextError/TextError";
import { useSelector, useDispatch } from "react-redux";
import { errorFunction } from "../../Component/Alert";
import { createBranch, updateBranch } from "../../Redux/Branch/thunk";
import Button from "../../Component/Button/Button";
import {
  checkRedundantDataBranchName,
  checkRedundantDataBranchCode,
} from "../../utils/redundant/branch";
import { useHistory } from "react-router-dom";

const CreateBranch = ({ currentPage }) => {
  // props
  const history = useHistory();
  const branch = useSelector((state) => state.branch.branch);
  const loading = useSelector((state) => state.branch.loading);
  const edit = useSelector((state) => state.branch.edit);
  const dispatch = useDispatch();
  //state for disabling the submit button
  const [lock, setLock] = useState(false);
  //initial state of the form
  const initialState = {
    name: edit ? branch?.name : "",
    code: edit ? branch?.code : "",
    is_active: edit ? (branch?.is_active ? true : true) : true,
    remarks: "",
  };

  //validation rules of the form
  const validationSchema = Yup.object().shape({
    name: Yup.string()
      .required("Name is required.")
      .min(1, "Name must be at least 1 characters.")
      .max(50, "Name must be at least 50 characters."),
    code: Yup.string()
      .required("Short name is required.")
      .min(1, "Short name must be at least 1 characters.")
      .max(5, "Short name must be at least 50 characters."),
    remarks:
      edit &&
      Yup.string()
        .required("Remarks is required.")
        .min(1, "Remarks Name must be at least 1 characters."),
    is_active: Yup.bool(),
  });
  const onSubmit = (values) => {
    const { name, code } = values;
    const capitalName = name?.charAt(0).toUpperCase() + name?.slice(1);
    const capitalShortName = code.toUpperCase();
    setLock(true);
    if (edit) {
      const id = branch?.id;
      dispatch(
        updateBranch(id, history, {
          ...values,
          name: capitalName,
          code: capitalShortName,
        })
      );
    } else {
      dispatch(
        createBranch(
          { ...values, name: capitalName, code: capitalShortName },
          history,
          currentPage
        )
      );
    }
    setLock(false);
  };

  // function which checks whether the branch is added already or not
  const handleChangeName = async (e, code) => {
    if (edit && e.target.value !== branch?.name) {
      if (e.target.value.trim() && e.target.value !== "") {
        const result = await checkRedundantDataBranchName(e, code);
        result
          ? errorFunction("Branch with this name is already added ") ||
            setLock(true)
          : setLock(false);
      }
    }
    if (!edit && e.target.value.trim() && e.target.value !== "") {
      const result = await checkRedundantDataBranchName(e, code);
      result
        ? errorFunction("Branch name with this  is already added. ") ||
          setLock(true)
        : setLock(false);
    }
  };
  const handleChangeCode = async (e, name) => {
    if (edit && e.target.value !== branch?.code) {
      if (e.target.value.trim() && e.target.value !== "") {
        const result = await checkRedundantDataBranchCode(e, name);
        result
          ? errorFunction("Branch with this name is already added ") ||
            setLock(true)
          : setLock(false);
      }
    }
    if (!edit && e.target.value.trim() && e.target.value !== "") {
      const result = await checkRedundantDataBranchCode(e, name);
      result
        ? errorFunction("Branch name with this  is already added. ") ||
          setLock(true)
        : setLock(false);
    }
  };
  return (
    <>
      <div className="row  mt-2">
        <div className="col-12 ">
          <div className="registration-process ">
            <Formik
              enableReinitialize={true}
              initialValues={initialState}
              validationSchema={validationSchema}
              onSubmit={onSubmit}
            >
              {(formik) => {
                return (
                  <Form autoComplete="off">
                    <div className="form-group row my-2 justify-content-center">
                      <div className="col-6">
                        <label htmlFor="name" className="fw-bold float-start">
                          Name
                          <strong className="text-danger ">*</strong>
                        </label>
                        <Field
                          type="text"
                          name="name"
                          className="form-control "
                          placeholder="Name"
                          onChange={(e) => {
                            formik.setFieldValue("name", e.target.value);
                            handleChangeName(e, formik.values.code);
                          }}
                          autoFocus
                        />
                        <ErrorMessage name="name" component={TextError} />
                      </div>
                    </div>
                    <div className="form-group row my-2 justify-content-center">
                      <div className="col-6">
                        <label htmlFor="code" className="fw-bold float-start">
                          Code
                          <strong className="text-danger ">*</strong>
                        </label>
                        <Field
                          type="text"
                          name="code"
                          className="form-control "
                          placeholder="Code"
                          onChange={(e) => {
                            formik.setFieldValue("code", e.target.value);
                            handleChangeCode(e, formik.values.name);
                          }}
                        />
                        <ErrorMessage name="code" component={TextError} />
                      </div>
                    </div>

                    <div className="form-group row my-2 justify-content-center">
                      {edit && (
                        <div className="col-3">
                          <label htmlFor="remarks" className="fw-bold">
                            Remarks
                            <span className="text-danger">*</span>
                          </label>
                          <textarea
                            type="text"
                            name="remarks"
                            onChange={(e) => {
                              formik.setFieldValue(
                                "remarks",
                                e.target.value.trim()
                              );
                            }}
                            className="form-control"
                            placeholder="Enter remarks"
                          />
                          <ErrorMessage name="remarks" component={TextError} />
                        </div>
                      )}
                    </div>
                    <div className="form-group row my-2 justify-content-center">
                      <div className="col-12 text-center">
                        <Field
                          type="checkbox"
                          name="is_active"
                          id="is_active"
                          className="filled-in chk-col-blue"
                        />
                        <label htmlFor="is_active" className="fw-bold mx-2">
                          Active
                        </label>
                      </div>
                    </div>
                    <div className="form-group text-center">
                      {!edit ? (
                        <Button
                          type={"btn"}
                          className={"btn btn-primary"}
                          loading={loading}
                          disabled={lock || !formik.dirty}
                          title={"Create"}
                          content={"Add"}
                        />
                      ) : (
                        <Button
                          type={"btn"}
                          className={"btn btn-primary "}
                          loading={loading}
                          disabled={lock}
                          title={"Update"}
                          content={"Update"}
                        />
                      )}
                    </div>
                  </Form>
                );
              }}
            </Formik>
          </div>
        </div>
      </div>
    </>
  );
};
export default CreateBranch;
