//get particular page
export const getPageBranch =
  ({ number, postsPerPage }) =>
  async (dispatch) => {
    try {
      dispatch(actions.loadingAction());
      const { data } = await API.getPageBranch(number, postsPerPage);
      dispatch(actions.getBranchSuccessAction(data));
    } catch (error) {
      dispatch(actions.getBranchFailAction(error));
      errorFunction(error);
    }
  };
export const createBranch =
  ({ name, code, is_active }, history) =>
  async (dispatch) => {
    try {
      const body = JSON.stringify({ name, code, is_active });
      dispatch(actions.loadingAction());
      const { data } = await API.createBranch(body);
      dispatch(actions.createBranchSuccessAction(data));
      successFunction("Branch Added Successfully.");
      history.push("/branch");
    } catch (error) {
      dispatch(actions.createBranchFailAction(error));
      errorFunction("Failed to create branch.");
    }
  };
// Delete  bank
export const deleteBranch = (id) => async (dispatch) => {
  try {
    dispatch(actions.loadingAction());
    const { data } = await API.deleteBranch(id);
    dispatch(actions.deleteBranchSuccessAction(data));
    successFunction("Branch Deleted Successfully.");
  } catch (error) {
    dispatch(actions.deleteBranchFailAction(error));
    errorFunction(error);
  }
};

//update
export const updateBranch = (id, history, values) => async (dispatch) => {
  try {
    dispatch(actions.loadingUpdatedAction());
    const { name, code, remarks, is_active } = values;
    const body = JSON.stringify({ name, code, remarks, is_active });
    const { data } = await API.updateBranch(id, body);
    dispatch(actions.updatedBranchSuccessAction(data));
    successFunction("Branch Updated Successfully ");
    history.push("/branch");
  } catch (error) {
    dispatch(actions.updatedBranchFailAction(error));
    errorFunction("Failed to Update Branch");
  }
};
//handle Search
export const handleSearch = (search, postsPerPage) => async (dispatch) => {
  try {
    dispatch(actions.loadingAction());
    const { data } = await API.handleSearch(search, postsPerPage);
    dispatch(actions.getBranchSuccessAction(data));
  } catch (error) {
    dispatch(actions.getBranchFailAction(error));
    errorFunction(error);
  }
};
